/* Nav */.menu {
  display: flex;
  position: fixed;
  width: 100vw;
  height: auto;
  z-index: 3;
  background-image: linear-gradient(-90deg, #2B2D42, #565977);
  padding: 2px 0px;
}

.menu-logo-container {
  flex-grow: 1;
  z-index: -2;
  margin: 20px 20px;
}

.menu-logo-container a {
  text-decoration: none;
  font-size: 1.15em;
  color: whitesmoke;
}

.menu-logo {
  width: 25px;
  height: 25px;
  padding: 15px;
}

.menu-icon {
  width: 30px;
  height: 30px;
  padding: 15px;
}

.menu-list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  list-style: none;
  text-align: center;
  width: 100vw;
  display: flex;
  justify-content: flex-end;
  margin: 0px;
}/* No mobile menu */.menu-element {
  font-size: 1.15rem;
  padding: 10px 2px -10px 2px;
  text-decoration: none;
  display: block;
  text-decoration: none;
  color: whitesmoke;
  margin: 10px 8px;
  position: relative;
  overflow: hidden;
  z-index: 0;
}

.menu-element,.menu-element:after,.menu-element:before {
  transition: all .5s;
}

.menu-element:after {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 1px;
  content: '.';
  color: transparent;
  visibility: none;
  opacity: 0;
  z-index: -1;
}

.menu-element:hover {
  font-size: 1.7rem;
  font-weight: 500;
}

nav ul li a:hover:after {
  opacity: 1;
  visibility: visible;
  height: 100%;
}

@media (min-width: 481px) and (max-width: 768px) {
  .menu-element {
    font-size: 1.35rem;
    padding: 10px;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  .menu-element {
    font-size: 1.45rem;
  }
}

@media (min-width: 1025px) and (max-width: 1439px) {
  .menu-element {
    font-size: 2.15rem;
    margin: 10px 30px;
    padding-top: 10px;
    padding-bottom: 10px;
  }
}

@media (min-width: 1440px) {
  .menu-element {
    font-size: 2.75rem;
    margin: 10px 30px;
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
